export default {
    name: "moreDisease",
    components: {},

    data() {
        return {
            diseaseList: []
        };
    },

    methods: {
        checkDetail(item){
            this.$router.push({
                path: "/diseaseDetail",
                query: {
                    diseaseObj: item
                }
            });
        }
    },
    created() {
        this.diseaseList = this.$route.query.diseaseList;
    }
};



